<template>
  <div>
    <van-nav-bar
      :fixed="true"
      title="用户审核"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="add" slot="right" size="20" /> -->
    </van-nav-bar>
    <div class="content">
      <div class="back">
        <mu-list
          textline="two-line"
          v-for="(item,index) in itemListContent"
          :key="index"
          class="bottom"
        >
          <mu-list-item avatar button :ripple="false" @click="toHandle(item.identity)">
            <mu-list-item-action>
              <mu-avatar @click="openAlertDialog1(item)">
                <img :src="item.photo!==null?url+item.photo[0]:''" alt srcset />
              </mu-avatar>
            </mu-list-item-action>
            <mu-list-item-content>
              <mu-list-item-title>{{item.username}}</mu-list-item-title>
              <mu-list-item-sub-title>
                申请成为：
                <span style="color:blue">{{item.roles[0]}}</span>
              </mu-list-item-sub-title>
              <!-- <mu-list-item-sub-title>申请成为{{item.userId}}</mu-list-item-sub-title> -->
            </mu-list-item-content>
            <mu-list-item-action @click="openAlertDialog(item.phone)">
              <div icon>
                <!-- <mu-icon value="info"></mu-icon> -->
                <span style="font-size:14px;color:orange">处理</span>
              </div>
            </mu-list-item-action>
          </mu-list-item>
        </mu-list>
      </div>
    </div>
    <mu-container>
      <mu-dialog
        title="用户申请审核！"
        width="600"
        max-width="80%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert"
      >
        <!-- <div class="positionAb">
          <img src="../../../static/img/close.png" alt />
        </div>-->
        <div v-for="(item,index) in alertContent" :key="index" class="backBorder">
          <div class="examineContent">
            <div class="width">
              <span>姓名</span>
            </div>
            <div class="inputBorder">
              <!-- <span>{{item.username}}</span> -->
              <input type="text" disabled class="input" :value="item.username" />
            </div>
          </div>
          <div class="examineContent">
            <div class="width">
              <span>申请时间</span>
            </div>
            <div class="inputBorder">
              <!-- <span>{{item.gmtCreate}}</span> -->
              <input type="text" disabled class="input" :value="item.gmtCreate" />
            </div>
          </div>
          <div class="examineContent">
            <div class="width">
              <span>电话号码</span>
            </div>
            <div class="inputBorder">
              <!-- <span>{{item.phone}}</span> -->
              <input type="text" disabled class="input" :value="item.phone" />
            </div>
          </div>
          <div class="examineContent">
            <div class="width">
              <span>身份证号</span>
            </div>
            <div class="inputBorder">
              <!-- <span>{{item.identity}}</span> -->
              <input type="text" disabled class="input" :value="item.identity" />
            </div>
          </div>
          <div class="examineContent">
            <div class="width">
              <span>专业</span>
            </div>
            <div class="inputBorder">
              <!-- <span>{{item.profession}}</span> -->
              <input type="text" disabled class="input" :value="item.profession" />
            </div>
          </div>
        </div>
        <mu-button slot="actions" flat color="primary" @click="openAlert=false">稍后处理</mu-button>
        <mu-button
          slot="actions"
          flat
          color="error"
          @click="closeAlertDialog(alertContent[0].phone,false)"
        >驳回</mu-button>
        <mu-button
          slot="actions"
          flat
          color="success"
          @click="closeAlertDialog(alertContent[0].phone,true)"
        >审核通过</mu-button>
      </mu-dialog>
    </mu-container>
    <mu-container>
      <mu-dialog
        title="用户从业经历"
        width="600"
        max-width="80%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert1"
      >
        <div v-for="(item,index) in alertContentList" :key="index" class="backBorder">
          <div class="examineContent">
            <div>
              <span>开始时间</span>
            </div>
            <div class="inputBorder">
              <!-- <span>{{item.startTime}}</span> -->
              <input type="text" :value="item.startTime" disabled class="input" />
            </div>
          </div>
          <div class="examineContent">
            <div>
              <span>结束时间</span>
            </div>
            <div class="inputBorder">
              <!-- <span>{{item.endTime}}</span> -->
              <input type="text" :value="item.endTime" disabled class="input" />
            </div>
          </div>
          <div class="examineContent">
            <div>
              <span>公司/企业</span>
            </div>
            <div class="inputBorder">
              <!-- <span>{{item.company}}</span> -->
              <input type="text" :value="item.company" disabled class="input" />
            </div>
          </div>
        </div>
        <mu-button slot="actions" flat color="success" @click="closeAlertDialog1">好的</mu-button>
      </mu-dialog>
    </mu-container>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax.js";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
export default {
  name: "Toexamine",
  data() {
    return {
      itemListContent: [],
      url: "http://47.112.16.168:",
      openAlert: false,
      alertContent: [],
      openAlert1: false,
      alertContentList: [],
      token: "",
      formdate: {}
    };
  },
  methods: {
    openAlertDialog1(e) {
      this.openAlert1 = true;
      var arr = [];
      arr = e.experience.innerMap;
      this.alertContentList = arr;
    },
    closeAlertDialog1() {
      this.openAlert1 = false;
    },
    /**
     * e 身份证号
     */
    openAlertDialog(e) {
      var arr = [];
      var arr1 = [];
      this.openAlert = true;
      this.alertContent = [];
      arr = this.itemListContent;
      for (let index = 0; index < arr.length; index++) {
        var name = arr[index].phone;
        if (e === name) {
          console.log(arr[index]);
          arr1.push(arr[index]);
          this.alertContent = arr1;
        }
      }
    },
    async closeAlertDialog(e, f) {
      this.openAlert = false;
      var url = "/system/user/reviewUser";
      this.formdate = new FormData();
      this.formdate.append("phone", e);
      this.formdate.append("isReview", f);
      var res = await AppBace.request(url, "POST", this.formdate, this.token);
      console.log(res);
      if (f) {
        Toast("用户审核通过");
      } else {
        Toast("用户已经驳回");
      }
      await this.initData();
    },
    /**
     * 返回
     */
    backTo() {
      this.$router.go(-1);
    },
    /**
     * 去审核
     * e是身份证号,
     * f是电话号码
     */
    toHandle(e, f) {},
    /**
     * 去掉图片的逗号
     */
    initImage(e) {
      for (let index = 0; index < e.length; index++) {
        if (e[index].photo !== null) {
          e[index].photo = e[index].photo.split(",");
        }
      }
      return e;
    },
    /**
     * e为token
     * 获取用户注册审核列表
     */
    async initData(e) {
      var url = "/system/user/reviewUser";
      var res = await AppBace.request(url, "GET", {}, e);
      this.itemListContent = res.data.data;
      this.itemListContent = this.initImage(this.itemListContent);
      console.log(res);
      if (res.data.data.length === 0) {
        Toast("暂无需要审核的用户");
      }
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.initData(this.token);
  }
};
</script>
<style scoped>
.content {
  margin-top: 46px;
  padding: 8px;
}
.back {
  background: white;
  border-radius: 10px;
}
.bottom {
  border-bottom: 1px solid rgb(240, 238, 238);
}
.mu-dialog-title {
  font-size: 14px;
}
.examineContent {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* border: 1px #ccc solid; */
  padding: 0 5px;
  border-radius: 5px;
}
.inputBorder {
  border: 1px solid #ccc;
  flex: 1;
  border-radius: 5px;
  margin-left: 5px;
  margin-bottom: 10px;
  text-align: center;
  background: white;
}
.backBorder {
  background: rgb(245, 244, 244);
  border-radius: 10px;
  border: 1px #ccc solid;
  /* margin-bottom: 10px; */
  box-shadow: 2px 5px 2px rgb(231, 241, 248);
  padding: 5px;
}
.input {
  border-radius: 10px;
  border: none;
  background: white;
  font-size: 12px;
}
.width {
  width: 80px;
}
/* .positionAb {
  position: relative;
  top: -20px;
  right: 10px;
} */
</style>


